import React,{Component} from 'react';

function Admins(props){
    function clear(){
        localStorage.setItem('drip_login',false);
        localStorage.setItem('drip_user','');
        localStorage.setItem('drip_code','');
        global.public.msg('/index','注销成功！',6,props.my);
    }
    let user_name = localStorage.getItem('drip_user');
    let admin = <div><span>欢迎{user_name}登录</span><button onClick={clear}>注销</button></div>;
    return admin;
}

class Admin extends Component {
    constructor(){
        super();
        this.state = {
            code:false
        }
    }

    componentDidMount(){
        global.public.login_code(this);
    }

    render(){
        return (
          <Admins my={this} />
        )
    }
}

export default Admin;
